interface AddPropertyModalProps {
  onClose: () => void;
  onPropertyAdded?: (properties: Property[]) => void;
}

const AddPropertyModal: React.FC<AddPropertyModalProps> = ({ onClose, onPropertyAdded }) => {
  const [isDragOver, setIsDragOver] = useState(false);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const fileInputRef = useRef<HTMLInputElement>(null);

  //   
  const [addressSearch, setAddressSearch] = useState('');
  const [searchResult, setSearchResult] = useState<{
    address: string;
    lat: number;
    lng: number;
  } | null>(null);

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
    
    const files = e.dataTransfer.files;
    if (files.length > 0) {
      const file = files[0];
      if (file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || 
          file.type === 'application/vnd.ms-excel' ||
          file.name.endsWith('.xlsx') || 
          file.name.endsWith('.xls')) {
        setSelectedFile(file);
      } else {
        alert(' (.xlsx, .xls)  .');
      }
    }
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      const file = files[0];
      if (file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || 
          file.type === 'application/vnd.ms-excel' ||
          file.name.endsWith('.xlsx') || 
          file.name.endsWith('.xls')) {
        setSelectedFile(file);
      } else {
        alert(' (.xlsx, .xls)  .');
      }
    }
  };

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const downloadTemplate = () => {
    console.log('===     ===');
    
    //   
    const headers = [
      '',
      '', 
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '/',
      '/',
      '/',
      '',
      '',
      '',
      '',
      '',
      ''
    ];
    
    //  
    const sampleData = [
      [
        '001',
        '  ',
        '   .   .',
        '',
        '',
        '850000000',
        '0',
        '   123-45',
        '', //  -  
        '', //  -  
        '84.5',
        '3/2',
        '3/15',
        '',
        '',
        '25.07.19',
        '',
        '02-1234-5678',
        'kim@realestate.com'
      ],
      [
        '002',
        '  ',
        '  5  .',
        '',
        '',
        '500000',
        '10000000',
        '   456-78',
        '', //  -  
        '', //  -  
        '25.3',
        '1/1',
        '2/5',
        '',
        '',
        '25.07.18',
        '',
        '02-9876-5432',
        'park@realestate.com'
      ]
    ];
    
    //  
    const workbook = XLSX.utils.book_new();
    const worksheet = XLSX.utils.aoa_to_sheet([headers, ...sampleData]);
    
    //   
    const colWidths = headers.map(() => ({ wch: 15 }));
    worksheet['!cols'] = colWidths;
    
    //  
    XLSX.utils.book_append_sheet(workbook, worksheet, '');
    
    //     
    const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
    const blobData = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
    const url = window.URL.createObjectURL(blobData);
    const link = document.createElement('a');
    link.href = url;
    link.download = '__.xlsx';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
    
    console.log('   ');
  };

  const handleUpload = async () => {
    if (!selectedFile) {
      alert(' .');
      return;
    }

    setIsUploading(true);
    setUploadProgress(0);

    try {
      //   
      const reader = new FileReader();
      reader.onload = async (e) => {
        try {
          const excelData = new Uint8Array(e.target?.result as ArrayBuffer);
          const workbook = XLSX.read(excelData, { type: 'array' });
          const sheetName = workbook.SheetNames[0];
          const worksheet = workbook.Sheets[sheetName];
          const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });

          console.log(' :', jsonData);

          //    
          const headers = jsonData[0] as string[];
          const rows = jsonData.slice(1) as any[][];

          console.log(':', headers);
          console.log('  :', rows.length);

          //   
          const propertyNumberIndex = headers.findIndex(header => 
            header?.toLowerCase().includes('') || 
            header?.toLowerCase().includes('')
          );
          
          console.log('  :', propertyNumberIndex);
          console.log(' :', headers[propertyNumberIndex]);

          //     ( Google Geocoding API )
          const convertAddressToCoordinates = async (address: string): Promise<{lat: number, lng: number} | null> => {
            try {
              console.log(`  : "${address}"`);
              
              if (!address || address.trim() === '') {
                console.log(' ');
                return null;
              }

              // Google Geocoding API 
              const response = await fetch(
                `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(address)}&key=AIzaSyCgPbhfAQ9gZbn4SVZIJoiLeHeIZek3-Pk&language=ko`
              );
              
              if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
              }
              
              const data = await response.json();
              console.log('Geocoding API :', data);
              
              if (data.status === 'OK' && data.results && data.results.length > 0) {
                const location = data.results[0].geometry.location;
                console.log(`  : "${address}"  ${location.lat}, ${location.lng}`);
                return {
                  lat: location.lat,
                  lng: location.lng
                };
              } else {
                console.log(`  : "${address}" - ${data.status}`);
                return null;
              }
              
            } catch (error) {
              console.error('  :', error);
              return null;
            }
          };

          //     
          const processedData = [];
          for (let i = 0; i < rows.length; i++) {
            const row = rows[i];
            if (!row || row.length === 0) continue;
            
            try {
              //  
              const id = row[propertyNumberIndex]?.toString() || `auto_${Date.now()}_${i}`;
              const title = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || ' ';
              const description = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || ' ';
              const type = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString()?.toLowerCase().includes('') ? 'rent' : 'sale';
              const propertyType = getPropertyType(row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString());
              const price = parseFloat(row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || '0');
              const deposit = parseFloat(row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || '0');
              const address = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || ' ';
              
              //   
              let lat = parseFloat(row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || '0');
              let lng = parseFloat(row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || '0');
              
              // /  0     
              if ((lat === 0 && lng === 0) || (isNaN(lat) || isNaN(lng))) {
                console.log(` ${i}:     - "${address}"`);
                const coordinates = await convertAddressToCoordinates(address);
                if (coordinates) {
                  lat = coordinates.lat;
                  lng = coordinates.lng;
                  console.log(` ${i}:    - ${lat}, ${lng}`);
                } else {
                  console.log(` ${i}:   ,  `);
                  lat = 37.4000; //    
                  lng = 126.7052;
                }
              }
              
              const area = parseFloat(row[headers.findIndex(header => header?.toLowerCase().includes('/'))]?.toString() || '0');
              const bedrooms = parseBedrooms(row[headers.findIndex(header => header?.toLowerCase().includes('/'))]?.toString());
              const bathrooms = parseBathrooms(row[headers.findIndex(header => header?.toLowerCase().includes('/'))]?.toString());
              const floor = row[headers.findIndex(header => header?.toLowerCase().includes('/'))]?.toString() || '';
              const parking = parseBoolean(row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString());
              const elevator = parseBoolean(row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString());
              const confirmedDate = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || '';
              const contactName = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || '';
              const contactPhone = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || '02-0000-0000';
              const contactEmail = row[headers.findIndex(header => header?.toLowerCase().includes(''))]?.toString() || 'contact@realestate.com';
              
              const property: Property = {
                id,
                title,
                description,
                price,
                deposit: deposit > 0 ? deposit : undefined,
                type,
                propertyType,
                address,
                location: { lat, lng },
                bedrooms,
                bathrooms,
                area,
                images: ['https://via.placeholder.com/300x200'],
                contact: {
                  name: contactName,
                  phone: contactPhone,
                  email: contactEmail
                },
                features: [],
                createdAt: new Date(),
                isActive: true,
                confirmedDate: confirmedDate || undefined,
                floor: floor || undefined,
                parking,
                elevator
              };
              
              console.log(` ${i} :`, property);
              processedData.push(property);
              
            } catch (error) {
              console.error(` ${i}   :`, error);
            }
          }
          
          console.log('  :', processedData.length);
          
          if (processedData.length > 0) {
            //     
            if (onPropertyAdded) {
              onPropertyAdded(processedData);
            }
            
            alert(` ${processedData.length}   .`);
            onClose(); //  
          } else {
            alert('     .');
          }
          
        } catch (error) {
          console.error('  :', error);
          alert('     .');
        } finally {
          setIsUploading(false);
          setUploadProgress(100);
        }
      };

      reader.readAsArrayBuffer(selectedFile);

    } catch (error) {
      console.error('  :', error);
      alert('    .');
      setIsUploading(false);
    }
  };

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  //   
  const searchAddress = async () => {
    if (!addressSearch.trim()) {
      alert(' .');
      return;
    }

    try {
      console.log(`  : "${addressSearch}"`);
      
      // Google Geocoding API 
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(addressSearch)}&key=AIzaSyCgPbhfAQ9gZbn4SVZIJoiLeHeIZek3-Pk&language=ko`
      );
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      console.log('Geocoding API :', data);
      
      if (data.status === 'OK' && data.results && data.results.length > 0) {
        const location = data.results[0].geometry.location;
        const formattedAddress = data.results[0].formatted_address;
        
        setSearchResult({
          address: formattedAddress,
          lat: location.lat,
          lng: location.lng
        });
        
        console.log(`  : "${addressSearch}"  ${formattedAddress} (${location.lat}, ${location.lng})`);
      } else {
        alert(`   : ${addressSearch}`);
        console.log(`  : "${addressSearch}" - ${data.status}`);
      }
      
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    }
  };

  const copyCoordinates = () => {
    if (searchResult) {
      const text = `${searchResult.lat}, ${searchResult.lng}`;
      navigator.clipboard.writeText(text).then(() => {
        alert('  !');
      }).catch(() => {
        alert('  .');
      });
    }
  };

  //   
  const getPropertyType = (type: string | undefined): 'commercial' | 'office' | 'building' | 'other' => {
    if (!type) return 'commercial';
    const lowerType = type.toLowerCase();
    if (lowerType.includes('')) return 'commercial';
    if (lowerType.includes('')) return 'office';
    if (lowerType.includes('')) return 'building';
    return 'other';
  };
  
  //    
  const parseBedrooms = (bedroomsStr: string | undefined): number => {
    if (!bedroomsStr) return 0;
    const match = bedroomsStr.match(/(\d+)/);
    return match ? parseInt(match[1]) : 0;
  };
  
  //    
  const parseBathrooms = (bathroomsStr: string | undefined): number => {
    if (!bathroomsStr) return 0;
    const match = bathroomsStr.match(/(\d+)/);
    return match ? parseInt(match[1]) : 0;
  };
  
  //    
  const parseBoolean = (value: string | undefined): boolean => {
    if (!value) return false;
    const lowerValue = value.toLowerCase();
    return lowerValue.includes('') || lowerValue.includes('') || lowerValue.includes('true') || lowerValue.includes('1');
  };

  return (
    <ModalOverlay onClick={onClose}>
      <ModalContent onClick={(e) => e.stopPropagation()}>
        <ModalHeader>
          <ModalTitle>  </ModalTitle>
          <CloseButton onClick={onClose}>&times;</CloseButton>
        </ModalHeader>

        <UploadSection>
          <TemplateSection>
            <TemplateTitle>   </TemplateTitle>
            <TemplateDescription>
                     .
                    .
              <br /><br />
              <strong>    :</strong>
              <br />
                 /  
              <br />
               /   0 
              <br />
               : "   123-45"    
            </TemplateDescription>
            <TemplateButton onClick={downloadTemplate}>
                 
            </TemplateButton>
          </TemplateSection>

          <FileUploadArea
            isDragOver={isDragOver}
            hasFile={!!selectedFile}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
            onClick={handleUploadClick}
          >
            <UploadIcon></UploadIcon>
            <UploadText>
              {selectedFile ? ' ' : '    '}
            </UploadText>
            <UploadSubtext>
              {selectedFile ? '   ' : ' : .xlsx, .xls'}
            </UploadSubtext>
            
            {selectedFile && (
              <FileInfo>
                <FileIcon></FileIcon>
                <div>
                  <FileName>{selectedFile.name}</FileName>
                  <FileSize> ({formatFileSize(selectedFile.size)})</FileSize>
                </div>
              </FileInfo>
            )}
          </FileUploadArea>

          <FileInput
            ref={fileInputRef}
            type="file"
            accept=".xlsx,.xls"
            onChange={handleFileSelect}
          />

          <AddressSearchSection>
            <AddressSearchTitle> </AddressSearchTitle>
            <AddressSearchDescription>
                /     .
                  /  .
            </AddressSearchDescription>
            
            <AddressSearchInput
              type="text"
              placeholder="  (:  )"
              value={addressSearch}
              onChange={(e) => setAddressSearch(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && searchAddress()}
            />
            
            <div style={{ display: 'flex', gap: '0.5rem' }}>
              <AddressSearchButton 
                onClick={searchAddress}
                disabled={!addressSearch.trim()}
              >
                {searchResult ? '  ' : '  '}
              </AddressSearchButton>
              
              {searchResult && (
                <AddressSearchButton onClick={copyCoordinates}>
                    
                </AddressSearchButton>
              )}
            </div>
            
            {searchResult && (
              <CoordinateResult>
                <strong> :</strong><br />
                : {searchResult.lat.toFixed(6)}<br />
                : {searchResult.lng.toFixed(6)}
              </CoordinateResult>
            )}
          </AddressSearchSection>

          <ProgressSection className={isUploading ? 'visible' : ''}>
            <ProgressText> ... {uploadProgress}%</ProgressText>
          </ProgressSection>

          <ButtonGroup>
            <Button 
              onClick={handleUpload}
              disabled={!selectedFile || isUploading}
            >
              {isUploading ? ' ...' : ' '}
            </Button>
            <CancelButton onClick={onClose}>
              
            </CancelButton>
          </ButtonGroup>
        </UploadSection>
      </ModalContent>
    </ModalOverlay>
  );
};

export default AddPropertyModal; 